interface CarContextType {
    cars: Car[];
    addCar: (car: Omit<Car, "id">) => void;
    editCar: (car: Car) => void;
    deleteCar: (id: string) => void;
}

const CarContext = createContext<CarContextType | undefined>(undefined);

export function CarProvider({ children }: { children: React.ReactNode }) {
    const [cars, setCars] = useState<Car[]>([]);
    const [hasLoaded, setHasLoaded] = useState(false);

    // Load from localStorage on mount
    useEffect(() => {
        const saved = localStorage.getItem("ambidrive_cars");
        if (saved) {
            try {
                setCars(JSON.parse(saved));
            } catch (e) {
                console.error("Failed to parse cars from localStorage", e);
                setCars(initialCars);
            }
        } else {
            setCars(initialCars);
        }
        setHasLoaded(true);
    }, []);

    // Save to localStorage on change
    useEffect(() => {
        if (hasLoaded) {
            localStorage.setItem("ambidrive_cars", JSON.stringify(cars));
        }
    }, [cars, hasLoaded]);

    const addCar = (carData: Omit<Car, "id">) => {
        const newCar: Car = {
            ...carData,
            id: Date.now().toString(),
        };
        setCars((prev) => [...prev, newCar]);
    };

    const editCar = (updatedCar: Car) => {
        setCars((prev) =>
            prev.map((car) => (car.id === updatedCar.id ? updatedCar : car))
        );
    };

    const deleteCar = (id: string) => {
        setCars((prev) => prev.filter((car) => car.id !== id));
    };

    if (!hasLoaded) {
        return null; // Or a loading spinner
    }

    return (
        <CarContext.Provider value={{ cars, addCar, editCar, deleteCar }}>
            {children}
        </CarContext.Provider>
    );
}

export function useCarContext() {
    const context = useContext(CarContext);
    if (context === undefined) {
        throw new Error("useCarContext must be used within a CarProvider");
    }
    return context;
}
